
#nav-list{position:fixed;left:0;bottom:0;top:60px;padding:20px 40px;line-height:40px;background-color:#293c55;overflow-y:auto;z-index:15
}
#nav-list .nav-left{width:168px
}
#nav-list .nav-item a{text-decoration:none;color:#b2b2b2;font-size:14px
}
#nav-list .nav-item a:hover{color:rgba(255,255,255,0.8)
}
#nav-list .nav-item a.router-active{color:rgba(255,255,255,0.9)
}


